'use client';

import React from 'react';
import { EditorLayout } from './EditorLayout';
import { usePageEditor, EditorBlock } from '@/hooks/usePageEditor';

interface PageEditorProps {
    page: {
        id: string;
        title: string;
        slug: string;
        blocks: any[];
    };
    templates: any[];
}

export const PageEditor: React.FC<PageEditorProps> = ({
    page,
    templates
}) => {
    // Transform initial blocks to match EditorBlock interface
    const initialBlocks: EditorBlock[] = page.blocks.map(block => ({
        id: block.id,
        templateId: block.templateId,
        config: block.config || {},
        order: block.order,
        template: block.template
    }));

    const {
        blocks,
        pageData,
        selectedBlockId,
        setSelectedBlockId,
        addBlock,
        updateBlockConfig,
        updatePageData,
        removeBlock,
        reorderBlocks,
        savePage,
        isSaving,
        isDirty
    } = usePageEditor({
        pageId: page.id,
        initialBlocks,
        initialPageData: {
            title: page.title,
            slug: page.slug,
            description: page.description,
            seoTitle: page.seoTitle,
            seoDescription: page.seoDescription,
            seoKeywords: page.seoKeywords,
            status: page.status,
        }
    });

    return (
        <EditorLayout
            pageTitle={pageData.title || page.title}
            pageId={page.id}
            pageSlug={pageData.slug || page.slug}
            pageData={pageData}
            blocks={blocks}
            templates={templates}
            selectedBlockId={selectedBlockId}
            isSaving={isSaving}
            isDirty={isDirty}
            onAddBlock={addBlock}
            onSelectBlock={setSelectedBlockId}
            onRemoveBlock={removeBlock}
            onReorderBlocks={reorderBlocks}
            onUpdateBlockConfig={updateBlockConfig}
            onUpdatePageData={updatePageData}
            onSave={savePage}
        />
    );
};
