import { useState, useCallback, useEffect } from 'react';
import { toast } from 'react-hot-toast';
import { useRouter } from 'next/navigation';

export interface EditorBlock {
    id: string;
    templateId: string;
    config: any;
    order: number;
    template?: {
        id: string;
        name: string;
        slug: string;
        category: string;
        componentCode: string;
        defaultConfig: any;
        configSchema: any;
    };
}

interface UsePageEditorProps {
    pageId: string;
    initialBlocks?: EditorBlock[];
    initialPageData?: any;
}

export function usePageEditor({ pageId, initialBlocks = [], initialPageData = {} }: UsePageEditorProps) {
    const router = useRouter();
    const [blocks, setBlocks] = useState<EditorBlock[]>(initialBlocks);
    const [pageData, setPageData] = useState(initialPageData);
    const [selectedBlockId, setSelectedBlockId] = useState<string | null>(null);
    const [isSaving, setIsSaving] = useState(false);
    const [isDirty, setIsDirty] = useState(false);

    // Initialize blocks if provided later (e.g. after fetch) - only if blocks array is empty
    useEffect(() => {
        if (initialBlocks.length > 0 && blocks.length === 0) {
            setBlocks(initialBlocks);
        }
    }, [initialBlocks, blocks.length]);

    const addBlock = useCallback((template: any) => {
        const newBlock: EditorBlock = {
            id: `temp-${Date.now()}`, // Temporary ID until saved
            templateId: template.id,
            config: template.defaultConfig || {},
            order: blocks.length,
            template: template
        };

        setBlocks(prev => [...prev, newBlock]);
        setSelectedBlockId(newBlock.id);
        setIsDirty(true);
    }, [blocks.length]);

    const updateBlockConfig = useCallback((blockId: string, newConfig: any) => {
        setBlocks(prev => prev.map(block =>
            block.id === blockId ? { ...block, config: newConfig } : block
        ));
        setIsDirty(true);
    }, []);

    const removeBlock = useCallback((blockId: string) => {
        setBlocks(prev => prev.filter(block => block.id !== blockId));
        if (selectedBlockId === blockId) {
            setSelectedBlockId(null);
        }
        setIsDirty(true);
    }, [selectedBlockId]);

    const reorderBlocks = useCallback((newBlocks: EditorBlock[]) => {
        // Update order property based on index
        const orderedBlocks = newBlocks.map((block, index) => ({
            ...block,
            order: index
        }));
        setBlocks(orderedBlocks);
        setIsDirty(true);
    }, []);

    const updatePageData = useCallback((field: string, value: any) => {
        setPageData(prev => ({ ...prev, [field]: value }));
        setIsDirty(true);
    }, []);

    const savePage = useCallback(async () => {
        setIsSaving(true);
        try {
            // Save page data first
            const pageResponse = await fetch(`/api/admin/landing-pages/${pageId}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    title: pageData.title,
                    slug: pageData.slug,
                    description: pageData.description,
                    seoTitle: pageData.seoTitle,
                    seoDescription: pageData.seoDescription,
                    seoKeywords: pageData.seoKeywords,
                    status: pageData.status,
                }),
            });

            if (!pageResponse.ok) {
                const errorData = await pageResponse.json().catch(() => ({}));
                throw new Error(errorData.error || 'Failed to save page data');
            }

            // Then sync blocks
            const blocksResponse = await fetch(`/api/admin/landing-pages/${pageId}/sync-blocks`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    blocks: blocks.map(block => ({
                        id: block.id,
                        templateId: block.templateId,
                        config: block.config,
                        order: block.order,
                    })),
                }),
            });

            if (!blocksResponse.ok) {
                throw new Error('Failed to save blocks');
            }

            const data = await blocksResponse.json();

            // Update blocks with the server response (to get real IDs for temp blocks)
            if (data.page?.blocks) {
                setBlocks(data.page.blocks.map((block: any) => ({
                    id: block.id,
                    templateId: block.templateId,
                    config: block.config,
                    order: block.order,
                    template: block.template,
                })));
            }

            setIsDirty(false);
            toast.success('Page saved successfully');
            
            // Refresh will reload the page data from server
            router.refresh();
        } catch (error: any) {
            console.error('Error saving page:', error);
            toast.error(error.message || 'Failed to save page');
        } finally {
            setIsSaving(false);
        }
    }, [blocks, pageId, pageData, router]);

    return {
        blocks,
        pageData,
        selectedBlockId,
        setSelectedBlockId,
        addBlock,
        updateBlockConfig,
        updatePageData,
        removeBlock,
        reorderBlocks,
        savePage,
        isSaving,
        isDirty
    };
}
